/* src/components/NavBar/NavBar.module.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2vh 5vw; /* 使用视口单位设置内边距 */
    background-color: transparent; /* 透明背景 */
    color: #2C3E50; /* 深灰色文本 */
    font-family: 'Open Sans', sans-serif; /* 字体 */
    border-bottom: none; /* 移除底部边框 */
    box-shadow: none; /* 移除阴影效果 */
}

.navbarLeft {
    flex: 1;
}

.navbarName {
    font-size: 24px; /* 固定字体大小 */
    font-weight: bold;
    color: #0ABAB5; /* Tiffany蓝色 */
}

.navbarRight {
    display: flex;
    align-items: center;
}

.navbarLinks {
    list-style: none;
    display: flex;
    gap: 2vw; /* 使用视口单位设置间距 */
    transition: transform 0.3s ease-in-out;
}

.navbarLinks li {
    margin: 0;
}

.navbarLinks a {
    color: #0ABAB5; /* 链接颜色 */
    text-decoration: none;
    font-size: 18px; /* 固定字体大小 */
    font-weight: 600;
    transition: color 0.3s;
}

.navbarLinks a:hover {
    color: #2C3E50; /* 悬停颜色 */
    text-decoration: underline;
}

.menuButton {
    display: none;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
}

/* 响应式设计 */
@media (max-width: 768px) {
    .navbarRight {
        flex: 1;
        justify-content: flex-end;
    }

    .menuButton {
        display: block;
    }

    .navbarLinks {
        position: fixed;
        right: 0;
        top: 8vh; /* 确保与导航栏底部对齐 */
        height: calc(100% - 8vh); /* 确保与导航栏底部对齐 */
        width: 250px;
        background-color: #ffffff;
        flex-direction: column;
        align-items: flex-start;
        padding: 2vh 2vw;
        box-shadow: -4px 0 6px rgba(0, 0, 0, 0.1);
        transform: translateX(100%);
        z-index: 1000; /* 确保侧边栏在最前面 */
    }

    .navbarLinks.showSidebar {
        transform: translateX(0);
    }

    .overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5); /* 半透明背景 */
        z-index: 999; /* 确保覆盖侧边栏 */
    }
}
