/* src/components/Footer/Footer.module.css */
.footer {
    background-color: #ffffff; /* 白色背景 */
    color: #2C3E50; /* 深灰色文本 */
    text-align: center;
    padding: 1vh 5vw; /* 减小内边距 */
    box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.1); /* 阴影效果 */
    font-family: 'Open Sans', sans-serif; /* 字体 */
}

.footerContent {
    max-width: 1200px;
    margin: 0 auto;
}

.footer a {
    color: #0ABAB5; /* Tiffany蓝色 */
    text-decoration: none;
}

.footer a:hover {
    color: #2C3E50; /* 悬停颜色 */
    text-decoration: underline;
}
