/* src/components/ResearchCard/ResearchCard.module.css */
.card {
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 2vh;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
}

.fullCard {
    flex-direction: row;
    width: 100%; /* 占据整行 */
}

.halfCard {
    flex-direction: column;
    width: 100%; /* 设置宽度为100%以适应父容器 */
    display: flex;
    justify-content: space-between; /* 在竖直方向上分配空间 */
}

.poster {
    width: 50%; /* 设置宽度为50% */
    height: auto;
}

.fullDetails {
    padding: 2vh 2vw;
    flex: 1;
    width: 50%; /* 设置宽度为50% */
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.halfDetails {
    padding: 2vh 2vw;
    width: 100%; /* 设置宽度为100% */
    text-align: center; /* 文字居中 */
    display: flex;
    flex-direction: column;
    justify-content: center; /* 垂直居中对齐 */
}

h3 {
    color: #0ABAB5; /* Tiffany蓝色 */
}

p {
    color: #2C3E50; /* 深灰色文本 */
    margin-bottom: 1vh;
}

.keyPoints {
    list-style: disc;
    margin: 1vh 1vw;
    padding-left: 20px;
    text-align: left; /* 文字左对齐 */
}

.keyPoints li {
    margin-bottom: 0.5vh;
}

.link {
    color: #0ABAB5;
    text-decoration: none;
    font-weight: 600;
    transition: color 0.3s;
}

.link:hover {
    color: #2C3E50;
    text-decoration: underline;
}
