/* src/pages/Research.module.css */
.researchContainer {
    padding: 1vh 12vw; /* 使用视口单位设置内边距 */
    font-family: 'Open Sans', sans-serif; /* 字体 */
    color: #2C3E50; /* 深灰色文本 */
    background-color: #ffffff; /* 白色背景 */
}

.researchHeader {
    text-align: center;
    margin-bottom: 2vh;
}

.researchContent {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 2vw; /* 控制卡片之间的间距 */
    margin-bottom: 4vh;
}

h1 {
    color: #0ABAB5; /* Tiffany蓝色 */
}

.fullCardWrapper {
    width: 100%; /* 占据整行 */
}

.halfCardWrapper {
    width: 48%; /* 占据父容器的一半 */
    display: flex;
    align-items: stretch; /* 确保同一行卡片高度自动对齐 */
}

.rightAligned {
    margin-left: auto; /* 右对齐 */
}
