/* src/pages/Home.module.css */
.homeContainer {
    padding: 1vh 12vw; /* 使用视口单位设置内边距 */
    font-family: 'Open Sans', sans-serif; /* 字体 */
    color: #2C3E50; /* 深灰色文本 */
    background-color: #ffffff; /* 白色背景 */
}

.homeHeader {
    text-align: center;
    margin-bottom: 2vh;
}

.aboutMe,
.education,
.experience,
.researches,
.contact {
    margin-bottom: 4vh;
}

.no_bullet {
    list-style-type: none;
}

h1, h2 {
    color: #0ABAB5; /* Tiffany蓝色 */
}

p {
    color: #2C3E50; /* 深灰色文本 */
    font-size: 18px; /* 固定字体大小 */
    font-weight: 400;
    line-height: 1;
}

ul {
    color: #2C3E50; /* 深灰色文本 */
    font-size: 18px; /* 固定字体大小 */
    font-weight: 400;
    line-height: 1.6;
}

a {
    color: #0ABAB5;
    text-decoration: none;
    transition: color 0.3s;
}

a:hover {
    color: #2C3E50;
    text-decoration: underline;
}
